import React from 'react';

const getECRList=async()=>{
  const response=await fetch('https://codeforces.com/api/contest.list');
  const json=await response.json(); 
  return json.result
  .filter(contest=>/Educational\sCodeforces\sRound/.test(contest.name))
  .sort((a,b)=>a.startTimeSeconds<b.startTimeSeconds?-1:1);
};

class App extends React.Component{
    constructor(){
      super();
      this.state={isLoaded:false,contestList:[]};
    }

    componentDidMount(){
      getECRList()
      .then(arr=>{
        this.setState({
          isLoaded:true,
          contestList:arr
        });
      });
    }

    render(){
      if(this.state.isLoaded===false){
        return (
          <h1>now loading...</h1>
        );
      }
      return(
        <table border="1">
          <thead>
            <tr>
              <th>name</th>
              <th>id</th>
            </tr>
          </thead>
          <tbody>
            {this.state.contestList.map(contest=>{
              return (
                <tr key={contest.contestId}>
                  <td>{contest.name}</td>
                  <td>{contest.id}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      );
    }
}
export default App;